import React, { Component } from 'react';
import { CategoryInterface } from '../../api/api'; // eslint-disable-line import/extensions
import './MainPage.css';

interface MainPageProps {
  /* eslint-disable no-unused-vars */
  onClickCategoryCard: (route: string) => void;
  /* eslint-enable no-unused-vars */
  categories: CategoryInterface[];
}

class MainPage extends Component<MainPageProps> {
  renderCards = (categories: CategoryInterface[]): JSX.Element[] => {
    const { onClickCategoryCard } = this.props;
    return (categories).map((card: CategoryInterface): JSX.Element => (
      <li
        key={card.name}
        className="main-page__card-container card-container"
      >
        <div className="main-page__card card">
          <div className="main-page__card-front card-front">
            <div
              className="main-page__image"
              style={{ backgroundImage: `url(${card.image})` }}
            />
            <a
              className="main-page__card-link"
              href={`#/${card.name}`}
              onClick={() => onClickCategoryCard(card.name)}
            >
              <div className="main-page__card-text card-text">{card.name}</div>
            </a>
          </div>
        </div>
      </li>
    ));
  };

  render(): JSX.Element {
    const { categories } = this.props;
    return (
      <>
        <h2 className="page-title">
          Main Page
        </h2>
        <ul className="main-page__container cards-page-container">
          {this.renderCards(categories)}
        </ul>
      </>
    );
  }
}

export default MainPage;
