import React, { Component } from 'react';
import { ROUTES, APP_MODE, STORAGE } from '../constants/constants.ts';
import {
  CardStatisticsInterface,
  CategoryInterface,
  StatisticsInterface,
  CardsInterface,
  CardInterface,
  EmptyProps,
} from './api/api'; // eslint-disable-line import/extensions
import './App.css';
import CardsPage from './components/CardsPage/CardsPage.tsx';
import MainPage from './components/MainPage/MainPage.tsx';
import NavMenu from './components/NavMenu/NavMenu.tsx';
import StatisticsPage from './components/Statistics/Statistics.tsx';
import Switcher from './components/Switcher/Switcher.tsx';

interface AppState {
  isHiddenNavMenu: boolean;
  gameMode: string;
  activeRoute: string;
  categories: CategoryInterface[];
  statistics: StatisticsInterface;
  cards: CardsInterface;
  cardsListToRepeat: CardInterface[];
}

class App extends Component<EmptyProps, AppState> {
  constructor(props: EmptyProps) {
    super(props);
    this.state = {
      isHiddenNavMenu: true,
      gameMode: APP_MODE.train,
      activeRoute: ROUTES.mainPage,
      categories: [],
      statistics: { default: [] },
      cards: { default: [] },
      cardsListToRepeat: [],
    };
  }

  componentDidMount = async (): Promise<void> => {
    await this.getCardsInfoState();
    this.setStatisticsState();
  }

  getCardsInfoState = async (): Promise<void> => {
    const response = await fetch('./cards.json');
    const cardsInfo = await response.json();
    const categories = await cardsInfo[0].categories;
    const cards = await cardsInfo[1];
    this.setState({ cards, categories });
  }

  setStatisticsState = (): void => {
    const defaultStatistics = this.getDefaultStatistics();
    if (localStorage.getItem(STORAGE.statistics)) {
      const returnObj = JSON.parse(localStorage.getItem(STORAGE.statistics) || '');
      this.setState({ statistics: returnObj });
    } else {
      this.setState({ statistics: defaultStatistics });
    }
  }

  getDefaultStatistics = (): StatisticsInterface => {
    const { cards } = this.state;
    return Object.fromEntries(
      Object.entries(cards).map(([key, value]) => ([
        key,
        value.map((card: CardInterface) => ({
          category: key,
          word: card.word,
          translation: card.translation,
          trainings: 0,
          errors: 0,
          rightGuessed: 0,
        })),
      ])),
    );
  }

  resetStatistics = (): void => {
    const defaultStatistics = this.getDefaultStatistics();
    this.updateStatistics(defaultStatistics);
  }

  updateStatistics = (newStatistics: StatisticsInterface): void => {
    const obj = JSON.stringify(newStatistics);
    localStorage.setItem(STORAGE.statistics, obj);
    this.setState({ statistics: newStatistics });
  }

  toggleNavMenu = (): void => {
    const { isHiddenNavMenu } = this.state;
    if (isHiddenNavMenu) {
      this.setState({ isHiddenNavMenu: false });
    } else {
      this.setState({ isHiddenNavMenu: true });
    }
  };

  setActiveRoute = (route: string): void => {
    this.setState({ activeRoute: route });
  };

  changeAppMode = (): void => {
    const { gameMode } = this.state;
    if (gameMode === APP_MODE.train) {
      this.setState({ gameMode: APP_MODE.play });
    } else {
      this.setState({ gameMode: APP_MODE.train });
    }
  }

  setRepeatCardsList = async (SortedStatisticsArray: CardStatisticsInterface[]): Promise<void> => {
    const { cards } = this.state;
    const cardsListToRepeat = SortedStatisticsArray.map((statistics) => (
      cards[statistics.category].find((card) => (statistics.word === card.word && statistics.errors !== 0))
    )).filter((card) => card).slice(0, 8);
    if (cardsListToRepeat.length) {
      this.setState({ cardsListToRepeat: cardsListToRepeat as CardInterface[] });
      this.setState({ activeRoute: 'Repeat hard words' });
    } else {
      alert('There are no words to repeat!'); // eslint-disable-line no-alert
    }
  }

  resetRepeatMode = (): void => {
    this.setState({ cardsListToRepeat: [] });
  }

  // TODO: add react router instead this
  renderRoutePage = (): JSX.Element => {
    const {
      activeRoute, gameMode, statistics, cards, categories, cardsListToRepeat,
    } = this.state;
    if (activeRoute === ROUTES.mainPage) {
      return (
        <MainPage
          onClickCategoryCard={(route: string) => {
            this.setActiveRoute(route);
            if (cardsListToRepeat.length) this.resetRepeatMode();
          }}
          categories={categories}
        />
      );
    }
    if (activeRoute === ROUTES.statistics) {
      return (
        <StatisticsPage
          statistics={statistics}
          onResetStatistics={this.resetStatistics}
          onRepeatWords={(SortedStatisticsArray: CardStatisticsInterface[]) => {
            this.setRepeatCardsList(SortedStatisticsArray);
          }}
        />
      );
    }
    return (
      <CardsPage
        category={activeRoute}
        gameMode={gameMode}
        onFinishGame={() => {
          this.setActiveRoute(ROUTES.mainPage);
          if (cardsListToRepeat.length) this.resetRepeatMode();
        }}
        statistics={statistics}
        onChangeStatisticsData={(newStatistics: StatisticsInterface) => this.updateStatistics(newStatistics)}
        categoryCardsArray={cardsListToRepeat.length ? cardsListToRepeat : cards[activeRoute]}
      />
    );
  }

  renderHeader = (): JSX.Element => {
    const { isHiddenNavMenu } = this.state;
    return (
      <header className="header">
        <div className="header__container">
          <button
            className={`burger-btn ${isHiddenNavMenu ? '' : 'active'}`}
            onClick={this.toggleNavMenu}
            type="button"
          >
            <span />
          </button>
          <Switcher
            onClick={this.changeAppMode}
          />
        </div>
      </header>
    );
  }

  renderNavMenu = (): JSX.Element => {
    const {
      isHiddenNavMenu, activeRoute, categories, cardsListToRepeat,
    } = this.state;
    return (
      <NavMenu
        isHiddenNavMenu={isHiddenNavMenu}
        onClickNavItem={(route: string) => {
          this.setActiveRoute(route);
          this.toggleNavMenu();
          if (cardsListToRepeat.length) this.resetRepeatMode();
        }}
        onClickPageCover={this.toggleNavMenu}
        activeRout={activeRoute}
        categories={categories}
      />
    );
  }

  renderFooter = (): JSX.Element => (
    <footer className="footer">
      <div className="footer-container">
        <a className="github" href="https://github.com/VitaliKlm" target="_blank" rel="noopener noreferrer">
          github
        </a>
        <a className="rss" href="https://rs.school/js/" target="_blank" rel="noopener noreferrer">
          <span className="rss-year">&apos;21</span>
        </a>
      </div>
    </footer>
  )

  render(): JSX.Element {
    return (
      <>
        {this.renderHeader()}
        {this.renderNavMenu()}
        {this.renderRoutePage()}
        {this.renderFooter()}
      </>
    );
  }
}

export default App;
