import React, { Component } from 'react';
import {
  TABLE_HEADER_TEXT,
  TABLE_PROPERTIES,
  CALCULATED_PROPERTY,
  DEFAULT_SORT_PROPERTY,
  REPEAT_SORT_PROPERTY,
} from '../../../constants/constants.ts';
import {
  StatisticsInterface,
  CardStatisticsInterface,
} from '../../api/api'; // eslint-disable-line import/extensions
import './Statistics.css';

interface StatisticsPageProps {
  statistics: StatisticsInterface;
  onResetStatistics: () => void;
  /* eslint-disable no-unused-vars */
  onRepeatWords: (SortedStatisticsArray: CardStatisticsInterface[]) => void;
  /* eslint-enable no-unused-vars */
}

interface StatisticsState {
  isDescendingSort: boolean;
  property: keyof CardStatisticsInterface;
}

const defaultStatisticsState: StatisticsState = {
  isDescendingSort: true,
  property: DEFAULT_SORT_PROPERTY,
};

class StatisticsPage extends Component<StatisticsPageProps, StatisticsState> {
  constructor(props: StatisticsPageProps) {
    super(props);
    this.state = defaultStatisticsState;
  }

  getPersentGuessed = (card: CardStatisticsInterface): number => (
    Math.round((card.rightGuessed * 100) / (card.errors + card.rightGuessed)) || 0
  )

  getCardsStatisticsArray = (): CardStatisticsInterface[] => {
    const { statistics } = this.props;
    return Object.values(statistics).map((category) => ([
      category.map((card: CardStatisticsInterface) => (card)),
    ])).flat(2);
  }

  getSortedStatistics = (
    statisticsArray: CardStatisticsInterface[],
    property: keyof CardStatisticsInterface,
  ): CardStatisticsInterface[] => (
    statisticsArray.sort((
      a: CardStatisticsInterface,
      b: CardStatisticsInterface,
    ) => {
      if (property === CALCULATED_PROPERTY) {
        return (this.getPersentGuessed(a) > this.getPersentGuessed(b) ? 1 : -1);
      }
      return (a[property] > b[property] ? 1 : -1);
    })
  );

  getStatisticsView = (statistics: CardStatisticsInterface[]): JSX.Element[] => (
    statistics.map((card, index) => (
      <tr>
        <td>{index + 1}</td>
        <td>{card.category}</td>
        <td>{card.word}</td>
        <td>{card.translation}</td>
        <td>{card.trainings}</td>
        <td>{card.errors}</td>
        <td>{card.rightGuessed}</td>
        <td>{this.getPersentGuessed(card)}</td>
      </tr>
    ))
  )

  changeSortState = (property: keyof CardStatisticsInterface): void => {
    const { isDescendingSort } = this.state;
    this.setState({ property });
    this.setState({ isDescendingSort: !isDescendingSort });
  }

  getCardsToRepeat = (): CardStatisticsInterface[] => {
    const statisticsArray = this.getCardsStatisticsArray();
    return (this.getSortedStatistics(statisticsArray, REPEAT_SORT_PROPERTY));
  }

  renderStatisticsTable = (): JSX.Element[] => {
    const { property, isDescendingSort } = this.state;
    const statisticsArray = this.getCardsStatisticsArray();
    const sortedStatisticsArray = this.getSortedStatistics(statisticsArray, property);

    const sortedStatisticsByDirectionType = isDescendingSort
      ? sortedStatisticsArray
      : sortedStatisticsArray.reverse();
    return this.getStatisticsView(sortedStatisticsByDirectionType);
  };

  renderStatisticsBtns = (): JSX.Element => {
    const { onResetStatistics, onRepeatWords } = this.props;
    return (
      <div className="statistics__btn-container">
        <button
          className="statistics__repeat-btn"
          onClick={() => onRepeatWords(this.getCardsToRepeat())}
          type="button"
        >
          Repeat hard words
        </button>
        <button
          className="statistics__reset-btn"
          onClick={() => onResetStatistics()}
          type="button"
        >
          Reset statistics
        </button>
      </div>
    );
  }

  render(): JSX.Element {
    const { property } = this.state;
    return (
      <div className="statistics-page">
        <h2 className="page-title">Statistics</h2>
        {this.renderStatisticsBtns()}
        <table className="statistics__table">
          <tr className="statistics__table-header">
            <th>№</th>
            {TABLE_HEADER_TEXT.map((text: string, index: number) => (
              <th>
                <button
                  className={`table-header-btn ${property === TABLE_PROPERTIES[index] ? 'active' : ''}`}
                  onClick={() => this.changeSortState(TABLE_PROPERTIES[index])}
                  type="button"
                >
                  {text}
                  <div className="table-header-indicator" />
                </button>
              </th>
            ))}
          </tr>
          {this.renderStatisticsTable()}
        </table>
      </div>
    );
  }
}

export default StatisticsPage;
