import React, { Component } from 'react';
import './CardsPage.css';
import { ReactComponent as RotateSVG } from '../../../assets/icons/rotate.svg';
import { ReactComponent as RepeatSVG } from '../../../assets/icons/repeat-audio.svg';
import {
  STATISTICS_DATA,
  APP_MODE,
  failureAudioUrl,
  successAudioUrl,
  correctAudioUrl,
  errorAudioUrl,
  goldStarIconUrl,
  emptyStarIconUrl,
  GAME_STATUS,
  failureImageUrl, successImageUrl,
} from '../../../constants/constants.ts';
import {
  CardInterface,
  CardStatisticsInterface,
  StatisticsInterface,
} from '../../api/api'; // eslint-disable-line import/extensions

interface CardsPageProps {
  // TODO: rename to routeCategory
  category: string;
  gameMode: string;
  onFinishGame(): void;
  /* eslint-disable no-unused-vars */
  onChangeStatisticsData: (statistics: StatisticsInterface) => void;
  /* eslint-disable no-unused-vars */
  statistics: StatisticsInterface;
  // TODO: rename to categoryCardsList
  categoryCardsArray: CardInterface[];
}

interface CardsPageState {
  isFlippedCards: boolean[];
  isDisabledCards: boolean[];
  rating: boolean[];
  gameStatus: string;
}

const DEFAULT_CARDS_PAGE_STATE: CardsPageState = {
  isFlippedCards: [],
  isDisabledCards: [],
  rating: [],
  gameStatus: GAME_STATUS.notRunning,
};

class CardsPage extends Component<CardsPageProps, CardsPageState> {
  audio = new Audio();

  gameAudioList: string[] = [];

  guessedCardIndex = 0;

  constructor(props: CardsPageProps) {
    super(props);
    this.state = DEFAULT_CARDS_PAGE_STATE;
  }

  componentDidMount(): void {
    this.resetCardsStyles();
  }

  componentDidUpdate(prevProps: CardsPageProps): void {
    const { category, gameMode } = this.props;
    if (prevProps.category !== category) {
      this.resetGame();
      this.resetCardsStyles();
    }
    if (prevProps.gameMode === APP_MODE.play && gameMode === APP_MODE.train) {
      this.resetGame();
      this.resetCardsStyles();
    }
  }

  resetCardsStyles = (): void => {
    const { categoryCardsArray } = this.props;
    this.setState({
      isFlippedCards: new Array(categoryCardsArray.length).fill(false),
      isDisabledCards: new Array(categoryCardsArray.length).fill(false),
    });
  }

  // TODO: move card to a separate component, with functions for this component ?
  renderCards = (categoryCardsArray: CardInterface[]): JSX.Element[] => {
    const { isFlippedCards, isDisabledCards } = this.state;
    const { gameMode } = this.props;
    return categoryCardsArray.map((card: CardInterface, index: number): JSX.Element => (
      <div
        key={card.word}
        className="cards-page__card-container card-container"
        onMouseLeave={() => (gameMode === APP_MODE.train ? this.resetFlippedCard(index) : '')}
        role="none"
      >
        <div
          className={`cards-page__card card 
            ${isFlippedCards[index] ? 'flipped' : ''} 
            ${isDisabledCards[index] ? 'disabled' : ''} 
            ${gameMode === APP_MODE.play ? 'play-mode' : ''}`}
          role="none"
        >
          <div
            className="cards-page__card-front card-front"
            onClick={() => (
              gameMode === APP_MODE.train ? this.playAudio(card.audioSrc) : this.handleChosenCard(index, card)
            )}
            role="none"
          >
            <div className="cards-page__image" style={{ backgroundImage: `url(${card.image})` }} />
            <div className="cards-page__card-text card-text">{card.word}</div>
          </div>
          <div className="cards-page__card-back card-back">
            <div className="cards-page__image" style={{ backgroundImage: `url(${card.image})` }} />
            <div className="cards-page__card-text card-text">{card.translation}</div>
          </div>
          <button
            className="cards-page__rotate-btn"
            onClick={() => (gameMode === APP_MODE.train ? this.flipCard(index, card) : '')}
            type="button"
            aria-label="Show Translation"
          >
            <RotateSVG />
          </button>
        </div>
      </div>
    ));
  }

  playAudio = (src: string): void => {
    this.audio.currentTime = 0;
    this.audio.src = src;
    this.audio.play();
  }

  // TODO: split this functions to separate service files ?
  setIsFlippedCardState = (index: number, status: boolean): void => {
    const { isFlippedCards } = this.state;
    const newIsFlippedCards = isFlippedCards;
    newIsFlippedCards[index] = status;
    this.setState({ isFlippedCards: newIsFlippedCards });
  }

  flipCard = (index: number, activeCard: CardInterface): void => {
    this.setIsFlippedCardState(index, true);
    this.changeCardStatisticsProperty(activeCard, STATISTICS_DATA.trainings);
  }

  resetFlippedCard = (index: number): void => {
    this.setIsFlippedCardState(index, false);
  }

  startGame = (): void => {
    this.resetGame();
    this.setState({ gameStatus: GAME_STATUS.running });
    this.createRandomAudioList();
    this.playAudio(this.gameAudioList[this.guessedCardIndex]);
  }

  createRandomAudioList = (): void => {
    const { categoryCardsArray } = this.props;
    const gameAudioList = categoryCardsArray.map((card) => card.audioSrc);
    this.gameAudioList = gameAudioList.sort((): number => 0.5 - Math.random());
  }

  resetGame = (): void => {
    this.setState({ gameStatus: GAME_STATUS.notRunning, rating: [] });
    this.guessedCardIndex = 0;
    this.gameAudioList = [];
  }

  endGame = (): void => {
    const { rating } = this.state;
    const { onFinishGame } = this.props;
    if (rating.includes(false)) {
      setTimeout(() => {
        this.playAudio(failureAudioUrl);
      }, 500);
    } else {
      setTimeout(() => {
        this.playAudio(successAudioUrl);
      }, 500);
    }
    setTimeout(() => {
      this.setState({ gameStatus: GAME_STATUS.finished });
    }, 500);
    setTimeout(() => {
      onFinishGame();
    }, 3000);
  }

  repeatCardAudio = (): void => {
    this.playAudio(this.gameAudioList[this.guessedCardIndex]);
  }

  handleChosenCard = (index: number, activeCard: CardInterface): void => {
    const { categoryCardsArray } = this.props;
    const { gameStatus, isDisabledCards } = this.state;
    if (gameStatus === GAME_STATUS.running && !isDisabledCards[index]) {
      if (categoryCardsArray[index].audioSrc === this.gameAudioList[this.guessedCardIndex]) {
        this.handleCorrectChoise(index, activeCard);
      } else {
        this.handleErrorChoise();
      }
    }
  }

  handleCorrectChoise = (index: number, activeCard: CardInterface): void => {
    const { categoryCardsArray } = this.props;
    const nextCardIndex = this.guessedCardIndex + 1;
    this.guessedCardIndex = nextCardIndex;
    this.playAudio(correctAudioUrl);
    this.addRatingPoint(true);
    this.disableCard(index);
    this.changeCardStatisticsProperty(activeCard, STATISTICS_DATA.rightGuessed);

    if (this.guessedCardIndex === categoryCardsArray.length) {
      this.endGame();
    } else {
      setTimeout(() => {
        this.playAudio(this.gameAudioList[this.guessedCardIndex]);
      }, 1000);
    }
  }

  handleErrorChoise = (): void => {
    const { categoryCardsArray } = this.props;
    this.playAudio(errorAudioUrl);
    this.addRatingPoint(false);
    const currentAudioSrc = this.gameAudioList[this.guessedCardIndex];
    const currentCard = categoryCardsArray.find(
      (card: CardInterface) => card.audioSrc === currentAudioSrc,
    );
    if (currentCard !== undefined) {
      this.changeCardStatisticsProperty(currentCard, STATISTICS_DATA.errors);
    }
  }

  addRatingPoint = (status: boolean): void => {
    const { rating } = this.state;
    const newRating = rating;
    newRating.push(status);
    this.setState({ rating: newRating });
  }

  disableCard = (index: number): void => {
    const { isDisabledCards } = this.state;
    const newIsDisabledCards = isDisabledCards;
    newIsDisabledCards[index] = true;
    this.setState({ isDisabledCards: newIsDisabledCards });
  }

  // TODO: make property: keyof CardStatisticsInterface to make code shorter
  changeCardStatisticsProperty = (activeCard: CardInterface, property: keyof CardStatisticsInterface): void => {
    const { statistics, onChangeStatisticsData } = this.props;
    const { category } = activeCard;
    const newStatictics = statistics;
    const cardStatistics = newStatictics[category].find((item: CardStatisticsInterface) => (
      // dubble check in the find method because some words may have the same spelling;
      item.word === activeCard.word && item.translation === activeCard.translation
    ));
    if (!cardStatistics) {
      return;
    } if (property === STATISTICS_DATA.rightGuessed) {
      cardStatistics.rightGuessed += 1;
    } else if (property === STATISTICS_DATA.errors) {
      cardStatistics.errors += 1;
    } else {
      cardStatistics.trainings += 1;
    }
    onChangeStatisticsData(newStatictics);
  }

  renderRepeatBtn = (): JSX.Element => {
    const { gameMode } = this.props;
    return (
      <button
        className={
          `cards-page__repeat-btn 
        ${gameMode === APP_MODE.play ? '' : 'train-mode'}`
        }
        onClick={this.repeatCardAudio}
        type="button"
      >
        <RepeatSVG />
      </button>
    );
  }

  renderStartGameBtn = (): JSX.Element => {
    const { gameMode } = this.props;
    return (
      <button
        className={
          `cards-page__start-game-btn 
        ${gameMode === APP_MODE.play ? '' : 'train-mode'}`
        }
        onClick={this.startGame}
        type="button"
      >
        Start game
      </button>
    );
  }

  renderRatingBar = (): JSX.Element => {
    const { gameMode } = this.props;
    const { rating, gameStatus } = this.state;
    return (
      <div className={
        `cards-page__rating
        ${gameMode === APP_MODE.play ? '' : 'train-mode'}
        ${gameStatus === GAME_STATUS.finished ? 'end-game' : ''}`
      }
      >
        {rating.map((ratingPoint: boolean) => (
          <div
            style={{ backgroundImage: `url(${ratingPoint ? goldStarIconUrl : emptyStarIconUrl})` }}
            className="cards-page__rating-star"
          />
        ))}
      </div>
    );
  }

  render(): JSX.Element {
    const { gameStatus, rating } = this.state;
    const { category, categoryCardsArray } = this.props;
    return (
      <>
        <h2 className="page-title">
          {category}
        </h2>
        {this.renderRatingBar()}
        {gameStatus === GAME_STATUS.running ? this.renderRepeatBtn() : this.renderStartGameBtn()}
        <ul className={
          `cards-page__container cards-page-container 
          ${gameStatus === GAME_STATUS.finished ? 'hidden' : ''}`
        }
        >
          {this.renderCards(categoryCardsArray)}
        </ul>
        <div className={
          `cards-page__end-pop-up 
          ${gameStatus === GAME_STATUS.finished ? '' : 'hidden'}`
        }
        >
          <img
            className="cards-page__pop-up-img"
            src={`${rating.includes(false) ? failureImageUrl : successImageUrl}`}
            alt=""
          />
          <div className="cards-page__pop-up-text">
            {`${rating.filter((e) => e === false).length} errors!`}
          </div>
        </div>
      </>
    );
  }
}

export default CardsPage;
