import React from 'react';
import { ROUTES } from '../../../constants/constants.ts';
import { CategoryInterface } from '../../api/api'; // eslint-disable-line import/extensions
import './NavMenu.css';

interface NavMenuProps {
  /* eslint-disable no-unused-vars */
  onClickNavItem: (route: string) => void;
  /* eslint-enable no-unused-vars */
  onClickPageCover: () => void;
  activeRout: string;
  isHiddenNavMenu: boolean;
  categories: CategoryInterface[];
}

const NavMenu: React.FC<NavMenuProps> = (props: NavMenuProps) => {
  const { activeRout, isHiddenNavMenu, categories } = props;

  const getNavItem = (category: string): JSX.Element => (
    <li
      className={`sidebar__nav-item ${category === activeRout ? 'active' : ''}`}
      key={category}
    >
      <a
        onClick={() => { props.onClickNavItem(category); }}
        href={`#/${category}`}
      >
        {category}
      </a>
    </li>
  );

  return (
    <>
      <div
        className={`page-cover ${isHiddenNavMenu ? 'hidden' : ''}`}
        onClick={() => { props.onClickPageCover(); }}
        role="none"
      />
      <ul className={`sidebar__nav-menu ${isHiddenNavMenu ? 'hidden' : ''}`}>
        {getNavItem(ROUTES.mainPage)}
        {categories.map((category: CategoryInterface) => getNavItem(category.name))}
        {getNavItem(ROUTES.statistics)}
      </ul>
    </>
  );
};

export default NavMenu;
